/* Modern Professional DATE Digital Agristack Dashboard Styles */
:root {
  --brand-700: #15803d;
  --brand-600: #16a34a;
  --brand-500: #22c55e;
  --blue-500: #3b82f6;
  --purple-500: #8b5cf6;
  --ink-900: #0f172a;
  --ink-800: #1e293b;
  --ink-700: #334155;
  --ink-600: #475569;
  --ink-500: #64748b;
  --surface: #ffffff;
  --ring-brand: rgba(21,128,61,0.12);
}
.dashboard {
  min-height: 100vh;
  background:
    radial-gradient(1200px 600px at 10% -10%, rgba(34,197,94,0.06), transparent 60%),
    radial-gradient(900px 500px at 120% 10%, rgba(59,130,246,0.06), transparent 60%),
    linear-gradient(135deg, #f8fafc 0%, #eef2f7 100%);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  display: flex;
  flex-direction: column;
}

/* Optional glass helper */
.glass {
  background: rgba(255,255,255,0.7);
  -webkit-backdrop-filter: saturate(120%) blur(12px);
  backdrop-filter: saturate(120%) blur(12px);
  border: 1px solid rgba(255,255,255,0.35);
}

/* Top Frame - Modern Professional Header */
.dashboard-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 96px;
  background: #006838;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  box-shadow: 0 10px 28px rgba(2, 8, 23, 0.18);
  z-index: 1000;
  border-bottom: 1px solid rgba(255,255,255,0.15);
}

.header-left {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.logo-section {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  flex-direction: column;
}

.logo-title {
  font-size: 32px;
  font-weight: 800;
  color: #ffffff;
  margin: 0;
  letter-spacing: -0.5px;
}

.logo-subtitle {
  font-size: 14px;
  color: rgba(255,255,255,0.9);
  margin: 0;
  font-weight: 500;
}

/* Calendar pill inside header */
.calendar-pill {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 14px;
  background: rgba(255,255,255,0.18);
  border: 1px solid rgba(255,255,255,0.35);
  border-radius: 16px;
  color: #ffffff;
  font-weight: 600;
  margin-left: 0;
}

.header-calendar {
  margin-right: 16px;
}
.calendar-icon {
  filter: drop-shadow(0 1px 2px rgba(0,0,0,0.25));
}
.calendar-text {
  font-size: 14px;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

/* User Profile Dropdown - Modern Design */
.user-profile-dropdown {
  position: relative;
  display: inline-block;
  z-index: 100;
}

.user-profile-trigger {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px 16px;
  background: linear-gradient(135deg, #15803d 0%, #22c55e 100%);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: white;
  border: none;
  box-shadow: 0 4px 12px rgba(21, 128, 61, 0.25);
  font-weight: 600;
  font-size: 14px;
}

.user-profile-trigger:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(21, 128, 61, 0.35);
}

.user-avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
  color: white;
  text-transform: uppercase;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.user-email {
  font-size: 14px;
  color: white;
  font-weight: 500;
}

.dropdown-arrow {
  font-size: 12px;
  transition: transform 0.3s ease;
}

.dropdown-arrow.rotated {
  transform: rotate(180deg);
}

.user-dropdown-menu {
  position: absolute;
  top: calc(100% + 8px);
  right: 0;
  width: 300px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border: 1px solid #e2e8f0;
  z-index: 1000;
  overflow: hidden;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 0.3s ease;
  pointer-events: none;
}

.user-dropdown-menu.show {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
}

.dropdown-header {
  padding: 24px;
  background: linear-gradient(135deg, #15803d 0%, #22c55e 100%);
  color: white;
  display: flex;
  align-items: center;
  gap: 16px;
}

.user-avatar-large {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 20px;
  color: white;
  text-transform: uppercase;
  border: 3px solid rgba(255, 255, 255, 0.3);
}

.user-details {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.user-name-large {
  font-size: 18px;
  font-weight: 700;
  color: white;
  line-height: 1.2;
  margin-bottom: 4px;
}

.user-email {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.9);
}

.dropdown-actions {
  padding: 16px;
}

.dropdown-action-btn {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 14px 16px;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  color: #374151;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  margin-bottom: 8px;
}

.dropdown-action-btn:hover {
  background: #f0fdf4;
  color: #15803d;
  border-color: #15803d;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(21, 128, 61, 0.15);
}

.dropdown-action-btn.logout {
  color: #dc2626;
  background: #fef2f2;
  border-color: #fecaca;
}

.dropdown-action-btn.logout:hover {
  background: #fee2e2;
  color: #dc2626;
  border-color: #fca5a5;
}

.dropdown-action-btn i {
  font-size: 16px;
  width: 20px;
  text-align: center;
}

/* Sidebar - Modern Professional Design */
.dashboard-sidebar {
  width: 280px;
  background: #006838;
  color: white;
  padding: 24px 20px;
  position: fixed;
  height: 100vh;
  top: 96px;
  overflow-y: auto;
  box-shadow: 4px 0 24px rgba(2, 8, 23, 0.18);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}

.sidebar-header {
  margin-bottom: 32px;
  padding-bottom: 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  text-align: center;
}

.sidebar-welcome {
  margin: 0;
  font-size: 1.6rem;
  font-weight: 800;
  color: #ffffff;
  letter-spacing: 1px;
}

.sidebar-role {
  margin-top: 6px;
  font-size: 0.95rem;
  font-weight: 700;
  color: rgba(255,255,255,0.9);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 60px;
  margin-top: 60px;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 14px 18px;
  border-radius: 16px;
  color: rgba(255, 255, 255, 0.95);
  text-decoration: none;
  transition: all 0.3s ease;
  font-weight: 600;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.15);
  margin-bottom: 8px;
  position: relative;
  overflow: hidden;
  font-size: 15px;
  background: rgba(255,255,255,0.06);
  backdrop-filter: blur(6px);
}

.nav-item:hover {
  background: rgba(255, 255, 255, 0.12);
  color: white;
  transform: translateX(8px);
  border-color: rgba(255, 255, 255, 0.35);
  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.22);
}

.nav-item.active {
  background: linear-gradient(135deg, #053918, #053d1a);
  color: white;
  font-weight: 700;
  border-color: rgba(255, 255, 255, 0.55);
  box-shadow: 0 12px 34px rgba(0, 0, 0, 0.28);
  transform: translateX(6px);
}

.nav-item i {
  font-size: 1.2rem;
  width: 26px;
  text-align: center;
}

.nav-item .dropdown-arrow {
  margin-left: auto;
  font-size: 0.9rem;
  transition: transform 0.3s ease;
}

.nav-item:hover .dropdown-arrow {
  transform: rotate(180deg);
}

/* Left accent bar indicator */
.nav-item::before {
  content: '';
  position: absolute;
  left: 8px;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  height: 60%;
  border-radius: 8px;
  background: linear-gradient(180deg, #ffffff, rgba(255,255,255,0.6));
  transition: width 0.25s ease;
}

.nav-item:hover::before,
.nav-item.active::before {
  width: 4px;
}

/* Main Content Area - Modern Layout - Full Width */
.dashboard-main {
  flex: 1;
  margin-left: 280px;
  margin-top: 96px;
  padding: 24px;
  background:
    radial-gradient(900px 400px at 80% -10%, rgba(139,92,246,0.05), transparent 60%),
    radial-gradient(1000px 500px at -10% 0%, rgba(34,197,94,0.05), transparent 60%),
    #f8fafc;
  min-height: calc(100vh - 70px);
  overflow-y: auto;
  width: calc(100vw - 280px);
  max-width: none;
}

/* Greeting banner */
.greeting-banner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  width: 100%;
  padding: 16px 20px;
  border-radius: 16px;
  background: linear-gradient(135deg, #b5ffe7 0%, #9affeb 100%);
  border: 1px solid #fbbf24;
  box-shadow: 0 12px 30px rgba(146, 64, 14, 0.18);
  margin-bottom: 20px;
}
.greeting-emoji {
  font-size: 18px;
}
.greeting-left { display: flex; flex-direction: column; }
.greeting-title { font-size: 1.1rem; font-weight: 800; color: #1e293b; }
.greeting-subtitle { font-size: 0.95rem; color: #64748b; margin-top: 4px; }
.greeting-date {
  margin-left: 8px;
  font-size: 13px;
  color: #64748b;
}
.greeting-right { font-weight: 700; color: #15803d; }

/* Welcome Section - Hero Style */
.welcome-section {
  background: linear-gradient(135deg, #d7e193 0%, #29e1c2 100%);
  border-radius: 24px;
  padding: 40px;
  margin-bottom: 32px;
  box-shadow: 0 14px 38px rgba(16, 185, 129, 0.25);
  border: 1px solid #6ee7b7;
  text-align: center;
}

.welcome-title {
  font-size: 2.5rem;
  font-weight: 800;
  background: linear-gradient(135deg, #15803d 0%, #22c55e 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0 0 16px 0;
  line-height: 1.2;
}

.welcome-subtitle {
  font-size: 1.1rem;
  color: #64748b;
  margin: 0;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}

/* Dashboard Content - Full Width */
.dashboard-content {
  background: transparent;
  width: 100%;
  max-width: none;
  margin: 0;
  padding: 0;
}

/* Overview Section - Full Width */
.overview-section {
  background: #b2f4d594;
  border-radius: 20px;
  padding: 32px;
  margin-bottom: 32px;
  box-shadow: 0 10px 30px rgba(2, 8, 23, 0.08);
  border: 1px solid #e2e8f0;
  width: 100%;
  max-width: none;
  margin-left: 0;
  margin-right: 0;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 950px;
}

/* Custom scrollbar for overview sections */
.overview-section::-webkit-scrollbar {
  width: 8px;
}

.overview-section::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 4px;
}

.overview-section::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #15803d 0%, #16a34a 100%);
  border-radius: 4px;
}

.overview-section::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #16a34a 0%, #22c55e 100%);
}

/* Registration Section - Full Width with Scrolling */
.registration-section {
  background: #b2f4d594;
  border-radius: 20px;
  padding: 32px;
  margin-bottom: 32px;
  box-shadow: 0 10px 30px rgba(2, 8, 23, 0.08);
  border: 1px solid #e2e8f0;
  width: 100%;
  max-width: none;
  margin-left: 0;
  margin-right: 0;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 950px;
}

/* Custom scrollbar for registration sections */
.registration-section::-webkit-scrollbar {
  width: 8px;
}

.registration-section::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 4px;
}

.registration-section::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #15803d 0%, #16a34a 100%);
  border-radius: 4px;
}

.registration-section::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #16a34a 0%, #22c55e 100%);
}

.overview-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 32px;
  flex-wrap: wrap;
  gap: 20px;
}

.overview-title {
  font-size: 1.8rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 8px 0;
}

.overview-description {
  font-size: 1rem;
  color: #64748b;
  margin: 0;
  line-height: 1.5;
}

.overview-actions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  border-radius: 12px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 1px solid transparent;
  text-decoration: none;
}

.action-btn.primary {
  background: linear-gradient(135deg, #15803d 0%, #22c55e 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(21, 128, 61, 0.25);
}

.action-btn.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(21, 128, 61, 0.35);
}

.action-btn.secondary {
  background: rgba(0, 0, 0, 0.3);
  color: #000012;
  border-color: #e2e8f0;
}

.action-btn.secondary:hover {
  background: #f1f5f9;
  border-color: #cbd5e1;
  transform: translateY(-1px);
}

.action-btn.success {
  background: linear-gradient(135deg, #c2ed10 0%, #acb910 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(5, 150, 105, 0.25);
}

.action-btn.success:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(5, 150, 105, 0.35);
}

.action-btn.refresh {
  background: linear-gradient(135deg, #f59e0b 0%, #fbbf24 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.25);
}

.action-btn.refresh:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(245, 158, 11, 0.35);
}

/* New filter buttons */
.action-btn.today {
  background: linear-gradient(135deg, #0891b2 0%, #06b6d4 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(6, 182, 212, 0.25);
}
.action-btn.today:hover { transform: translateY(-2px); box-shadow: 0 8px 20px rgba(6, 182, 212, 0.35); }

.action-btn.month {
  background: linear-gradient(135deg, #7c3aed 0%, #a855f7 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(124, 58, 237, 0.25);
}
.action-btn.month:hover { transform: translateY(-2px); box-shadow: 0 8px 20px rgba(124, 58, 237, 0.35); }

.action-btn.year {
  background: linear-gradient(135deg, #15803d 0%, #22c55e 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(21, 128, 61, 0.25);
}
.action-btn.year:hover { transform: translateY(-2px); box-shadow: 0 8px 20px rgba(21, 128, 61, 0.35); }

/* Active state for filter buttons */
.action-btn.active {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.25);
  border: 2px solid #ffffff;
}

.action-btn.refresh.active {
  background: linear-gradient(135deg, #d97706 0%, #f59e0b 100%);
  box-shadow: 0 8px 20px rgba(245, 158, 11, 0.4);
}

.action-btn.today.active {
  background: linear-gradient(135deg, #0e7490 0%, #0891b2 100%);
  box-shadow: 0 8px 20px rgba(6, 182, 212, 0.4);
}

.action-btn.month.active {
  background: linear-gradient(135deg, #6d28d9 0%, #7c3aed 100%);
  box-shadow: 0 8px 20px rgba(124, 58, 237, 0.4);
}

.action-btn.year.active {
  background: linear-gradient(135deg, #166534 0%, #15803d 100%);
  box-shadow: 0 8px 20px rgba(21, 128, 61, 0.4);
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
  margin-bottom: 32px;
}

.stats-card {
  background: white;
  border-radius: 20px;
  padding: 32px;
  box-shadow: 0 12px 34px rgba(2, 8, 23, 0.1);
  border: 1px solid #e2e8f0;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

/* Medium tinted backgrounds for stats cards */
.stats-grid .stats-card:nth-child(1) {
  background: linear-gradient(180deg, #d9f7e6 0%, #c7f0d8 100%);
  border-color: #86efac;
}
.stats-grid .stats-card:nth-child(2) {
  background: linear-gradient(180deg, #dce7ff 0%, #cfe0ff 100%);
  border-color: #93c5fd;
}
.stats-grid .stats-card:nth-child(3) {
  background: linear-gradient(180deg, #efe2ff 0%, #ead6ff 100%);
  border-color: #c4b5fd;
}

.stats-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.12);
}

.stats-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #15803d 0%, #22c55e 100%);
}

.stats-icon {
  width: 64px;
  height: 64px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  font-size: 24px;
  color: white;
}

.stats-icon.farmers {
  background: linear-gradient(135deg, #15803d 0%, #22c55e 100%);
}

.stats-icon.employees {
  background: linear-gradient(135deg, #2563eb 0%, #3b82f6 100%);
}

.stats-icon.fpo {
  background: linear-gradient(135deg, #7c3aed 0%, #a855f7 100%);
}

.stats-title {
  font-size: 14px;
  font-weight: 600;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 8px;
}

.stats-value {
  font-size: 2.5rem;
  font-weight: 800;
  color: #1e293b;
  margin-bottom: 12px;
  line-height: 1;
}

.stats-change {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
  font-weight: 600;
}

.stats-change.positive {
  color: #059669;
}

.stats-change.negative {
  color: #dc2626;
}

.stats-change.neutral {
  color: #64748b;
}

/* Period indicator for filtered stats */
.stats-period-indicator {
  position: absolute;
  top: 12px;
  right: 12px;
  background: rgba(59, 130, 246, 0.1);
  color: #3b82f6;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 600;
  border: 1px solid rgba(59, 130, 246, 0.2);
}

/* Bottom Sections */
.bottom-sections {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 24px;
}

.section-card {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 24px;
  padding: 32px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(34, 197, 94, 0.1);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.section-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #22c55e 0%, #16a34a 50%, #15803d 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.section-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.12);
  border-color: rgba(34, 197, 94, 0.2);
}

.section-card:hover::before {
  opacity: 1;
}

/* Enhanced backgrounds for bottom section cards */
.bottom-sections .section-card:first-child {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-color: rgba(34, 197, 94, 0.15);
  box-shadow: 0 8px 32px rgba(34, 197, 94, 0.08);
}

.bottom-sections .section-card:first-child::before {
  background: linear-gradient(90deg, #22c55e 0%, #16a34a 50%, #15803d 100%);
}

.bottom-sections .section-card:last-child {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-color: rgba(168, 85, 247, 0.15);
  box-shadow: 0 8px 32px rgba(168, 85, 247, 0.08);
}

.bottom-sections .section-card:last-child::before {
  background: linear-gradient(90deg, #a855f7 0%, #9333ea 50%, #7c3aed 100%);
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 28px;
  padding-bottom: 16px;
  border-bottom: 2px solid rgba(34, 197, 94, 0.1);
  position: relative;
}

.section-header::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 60px;
  height: 2px;
  background: linear-gradient(90deg, #22c55e 0%, #16a34a 100%);
  border-radius: 1px;
}

/* Different header accents for each card */
.bottom-sections .section-card:first-child .section-header::after {
  background: linear-gradient(90deg, #22c55e 0%, #16a34a 100%);
}

.bottom-sections .section-card:last-child .section-header::after {
  background: linear-gradient(90deg, #a855f7 0%, #9333ea 100%);
}

.section-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0;
  position: relative;
}

.section-link {
  background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
  color: white;
  text-decoration: none;
  font-weight: 600;
  font-size: 13px;
  padding: 8px 16px;
  border-radius: 20px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(34, 197, 94, 0.2);
}

.section-link:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(34, 197, 94, 0.3);
}

/* Different link colors for each card */
.bottom-sections .section-card:last-child .section-link {
  background: linear-gradient(135deg, #a855f7 0%, #9333ea 100%);
  box-shadow: 0 2px 8px rgba(168, 85, 247, 0.2);
}

.bottom-sections .section-card:last-child .section-link:hover {
  box-shadow: 0 4px 12px rgba(168, 85, 247, 0.3);
}

/* Activities List */
.activities-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.activity-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 16px;
  border: 1px solid rgba(34, 197, 94, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.activity-item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background: linear-gradient(180deg, #22c55e 0%, #16a34a 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.activity-item:hover {
  background: linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 100%);
  transform: translateX(6px);
  border-color: rgba(34, 197, 94, 0.2);
  box-shadow: 0 4px 16px rgba(34, 197, 94, 0.1);
}

.activity-item:hover::before {
  opacity: 1;
}

.activity-content {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 12px;
}

.activity-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  flex-shrink: 0;
}

.activity-dot.success {
  background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
  box-shadow: 0 0 0 3px rgba(34, 197, 94, 0.2);
}

.activity-dot.error {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.2);
}

.activity-text {
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 4px;
  font-size: 14px;
}

.activity-time {
  font-size: 12px;
  color: #64748b;
  font-weight: 500;
}

.activity-badge {
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 11px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border: none;
  cursor: default;
  transition: all 0.3s ease;
}

.activity-badge.success {
  background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
  color: white;
  box-shadow: 0 2px 8px rgba(34, 197, 94, 0.3);
}

.activity-badge.pending {
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  color: white;
  box-shadow: 0 2px 8px rgba(245, 158, 11, 0.3);
}

/* Quick Actions Grid */
.quick-actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.quick-action-btn {
  display: flex;
  align-items: center;
  gap: 14px;
  padding: 24px;
  border-radius: 20px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid transparent;
  text-decoration: none;
  text-align: left;
  position: relative;
  overflow: hidden;
}

.quick-action-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.05) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.quick-action-btn:hover::before {
  opacity: 1;
}

.quick-action-btn i {
  font-size: 18px;
  position: relative;
  z-index: 1;
}

.quick-action-btn span {
  position: relative;
  z-index: 1;
}

.quick-action-btn.primary {
  background: linear-gradient(135deg, #15803d 0%, #22c55e 100%);
  color: white;
  box-shadow: 0 6px 20px rgba(21, 128, 61, 0.3);
  border: 1px solid rgba(34, 197, 94, 0.2);
}

.quick-action-btn.primary:hover {
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 12px 30px rgba(21, 128, 61, 0.4);
  border-color: rgba(34, 197, 94, 0.3);
}

.quick-action-btn.secondary {
  background: linear-gradient(135deg, #2563eb 0%, #3b82f6 100%);
  color: white;
  box-shadow: 0 6px 20px rgba(37, 99, 235, 0.3);
  border: 1px solid rgba(59, 130, 246, 0.2);
}

.quick-action-btn.secondary:hover {
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 12px 30px rgba(37, 99, 235, 0.4);
  border-color: rgba(59, 130, 246, 0.3);
}

.quick-action-btn.info {
  background: linear-gradient(135deg, #7c3aed 0%, #a855f7 100%);
  color: white;
  box-shadow: 0 6px 20px rgba(124, 58, 237, 0.3);
  border: 1px solid rgba(168, 85, 247, 0.2);
}

.quick-action-btn.info:hover {
  transform: translateY(-4px) scale(1.02);
  box-shadow: 0 12px 30px rgba(124, 58, 237, 0.4);
  border-color: rgba(168, 85, 247, 0.3);
}

/* Filters Section */
.filters-section {
