/* ===== MODERN FULL-WIDTH TABLE STYLES ===== */

/* Data Table Container - Full Width */
.data-table-container {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 16px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  margin: 24px 0;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  position: relative;
  max-height: 600px;
}

/* Table Scroll Wrapper - For inline table scrolling */
.table-scroll-wrapper {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 16px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  margin: 24px 0;
  padding: 10px;
  overflow: hidden;
  width: 100%;
  position: relative;
  max-height: 600px;
}

/* Custom scrollbar for data table container */
.data-table-container::-webkit-scrollbar {
  width: 8px;
}

.data-table-container::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 4px;
}

.data-table-container::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #15803d 0%, #16a34a 100%);
  border-radius: 4px;
}

.data-table-container::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #16a34a 0%, #22c55e 100%);
}

/* Modern Data Table */
.data-table {
  width: 100%;
  border-collapse: collapse;
  background: #ffffff;
  font-size: 14px;
  line-height: 1.5;
  max-height: 500px;
  overflow-y: auto;
  overflow-x: auto;
  display: block;
}

/* Table scrolling container */
.data-table thead,
.data-table tbody,
.data-table tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.data-table thead {
  width: calc(100% - 8px); /* Account for scrollbar */
}

/* Custom scrollbar for data table */
.data-table::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.data-table::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 4px;
}

.data-table::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #15803d 0%, #16a34a 100%);
  border-radius: 4px;
}

.data-table::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #16a34a 0%, #22c55e 100%);
}

/* Custom scrollbar for table scroll wrapper */
.table-scroll-wrapper::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.table-scroll-wrapper::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 4px;
}

.table-scroll-wrapper::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #15803d 0%, #16a34a 100%);
  border-radius: 4px;
}

.table-scroll-wrapper::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #16a34a 0%, #22c55e 100%);
}

/* Table Header - Modern Gradient */
.data-table thead {
  background: linear-gradient(135deg, #15803d 0%, #16a34a 50%, #22c55e 100%);
  position: sticky;
  top: 0;
  z-index: 10;
}

.data-table th {
  padding: 18px 20px;
  text-align: center;
  font-weight: 900;
  font-size: 15px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 2px solid #15803d;
  position: relative;
  white-space: nowrap;
}

.data-table th:not(:last-child)::after {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 60%;
  width: 1px;
  background: rgba(255, 255, 255, 0.2);
}

/* Table Body Rows */
.data-table tbody tr {
  transition: all 0.2s ease;
  border-bottom: 1px solid #f3f4f6;
}

.data-table tbody tr:hover {
  background: linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(21, 128, 61, 0.1);
}

.data-table tbody tr:last-child {
  border-bottom: none;
}

/* Table Cells */
.data-table td {
  padding: 16px 20px;
  color: #374151;
  font-weight: 500;
  border-right: 1px solid #f3f4f6;
  vertical-align: middle;
  text-align: center;
}

.data-table td:last-child {
  border-right: none;
}

/* Status Badges */
.status-approved,
.status-active {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: #ffffff;
  padding: 6px 12px;
  border-radius: 20px;
